import React from "react";
import "../index.css";

const List = props => {
  const { item } = props;
  return (
    <div className="list_box">
      <div className="left">
        <div className="line-item" style={{ textAlign: "left",cursor:'pointer' }}>
          {item.station}
        </div>
        <div className="line-item" style={{ textAlign: "center" }}>
          {item.city}
        </div>
      </div>
      <div className="line-item" style={{ textAlign: "right" }}>
        {item.time}
      </div>
    </div>
  );
};

export default List;
