import React, { Component } from 'react';
import './index.css'
class Header extends Component {
    state = {
        leftHeadList: [
            { href: 'https://job.alibaba.com/zhaopin/index.htm', name: '首页' },
            { href: 'https://job.alibaba.com/zhaopin/positionList.htm', name: '社会招聘' },
            { href: 'https://campus.alibaba.com', name: '校园招聘' },
            { href: 'https://job.alibaba.com/zhaopin/about.htm', name: '了解阿里' },
            { href: 'https://job.alibaba.com/zhaopin/apply.htm', name: '个人中心' },
        ],
        activeIndex: 0,
    }
    render() {
        let { leftHeadList, activeIndex } = this.state;
        return (
            <div className='head'>
                <div className='head-main'>
                    <div className='head-menu'>
                        <a href="https://job.alibaba.com/zhaopin/index.htm" className='left-logo-box'>
                            <img src="https://img.alicdn.com/tfs/TB1Zv8_lxSYBuNjSspjXXX73VXa-390-63.png" alt="logo" className='logo' />
                        </a>
                        <i style={{ float: 'left', color: 'white', fontStyle: 'normal', fontSize: '20px', lineHeight: '52px', margin: '0 10px', opacity: '0.8' }}>|</i>
                        <i style={{ float: 'left', color: 'white', fontStyle: 'normal', fontSize: '14px', lineHeight: '54px', opacity: '0.8' }}>社招官网</i>
                        <ul>
                            {leftHeadList.map((item, index) => {
                                return <li
                                    className={index === activeIndex ? 'current-cat' : ''}
                                    key={index}
                                    onClick={() => { this.changeActiveIndex(index) }}
                                ><a 
                                href={item.href}
                                >{index === 0?`${item.name[0] +'\xa0\xa0\xa0\xa0\xa0\xa0' + item.name[1]}`:item.name}</a></li>
                            })}
                            <div className='login'>
                                欢迎来到阿里巴巴集团招聘！
                                <a href="https://passport.alibaba.com/login.htm?appName=hrjob&amp;params=https%3A%2F%2Fjob.alibaba.com%2F%2Fzhaopin%2Findex.htm" target="_top">登录</a>
                                <span>&nbsp;|&nbsp;</span>
                                <a href="http://member1.taobao.com/member/newbie.htm">注册</a>
                            </div>
                        </ul>
                    </div>
                </div>
            </div>
        );

    }
    changeActiveIndex = (index) => {
        let { activeIndex } = this.state;
        if (activeIndex !== index) this.setState({ activeIndex: index })
    }
}

export default Header;