// export default RecruitList;
import React from "react";
import List from "./List";
import "./index.css";
class RecruitList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dataArr: [],
      duration: 0,
      postionTop: 0,
      codeTimer: null
    };
  }
  componentDidMount() {
    console.log(this.props);
    if (this.props.list && this.props.list.length) {
      this.setState(
        {
          duration: this.props.list.length * 2 * 50 - 250,
          dataArr: [...this.props.list]
        },
        () => {
          this.handlePostionTop();
        }
      );
    }
  }
  handlePostionTop = () => {
    const { duration, codeTimer, postionTop, dataArr } = this.state;
    const { list } = this.props;
    console.log("sas222a", duration);
    if (duration) {
      this.setState({
        codeTimer: setTimeout(() => {
          this.setState(
            {
              duration: duration - 1,
              postionTop: postionTop - 1
            },
            () => {
              this.handlePostionTop();
            }
          );
        }, 40)
      });
    } else {
      let dataList = JSON.parse(JSON.stringify(dataArr));
      let arr = dataList.slice(0, parseInt(dataArr.length));
      dataList.splice(0, parseInt(dataArr.length));
      dataList.push(...arr);
      console.log(dataList, dataArr, arr);
      this.setState(
        {
          dataArr: [...dataList],
          codeTimer: null,
          duration: this.props.list.length *2 * 50 - 250,
          postionTop: 0
        },
        () => {
          clearTimeout(codeTimer);
          this.handlePostionTop();
        }
      );
    }
  };
  handleScrollOver = () => {
    const { codeTimer } = this.state;
    if (codeTimer) clearTimeout(codeTimer)
  };
  handleScrollOut = () => {
    this.handlePostionTop();
  };
  render() {
    const { dataArr } = this.state;
    return (
      <div className="recruit_list_page_container">
        <div style={
          {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            padding: '0 14px',
            borderBottom: '1px solid rgba(31,56,88,0.06)',
            boxShadow: '0 0 15px 0 rgba(31,56,88,0.08)',
            height: '49px',
            lineHeight: '49px'
          }}>
          <div>最新职位</div>
          <span>更多</span>
        </div>
        <div className="bottom" style={{ background: '#fff' }}>
          <div
            className="list_content" 
            onMouseLeave = {()=>{this.handlePostionTop()}}
            onMouseEnter = {this.handleScrollOver}
          >
            <div
              className="list_postion"
              style={{ top: this.state.postionTop }}
            >
               {dataArr && dataArr.length
                ? this.props.list.map((el, i) => (
                    <List index={i} key={i} item={el} />
                  ))
                : null}
              {dataArr && dataArr.length
                ? this.props.list.map((el, i) => (
                    <List index={parseInt('2'+i)} key={i} item={el} />
                  ))
                : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RecruitList;
